.faculty-staff-card {

	.selected-result {
		
		label {
			font-size: 11px;
			font-weight: 500;
			text-transform: uppercase;
		}

		span {
			font-size: 14px;
			padding: 0 10px;
			margin-right: 5px;
		}
	}

	&.search-results {

		li {
			width: 280px;
			padding: 5px 15px;
			font-size: 14px;
			cursor: pointer;
		}
	}
}
